@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.6em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #3A3A3A;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
}
.container.margin-top {
  margin-top: 140px;
}

header {
  width: 100%;
  height: 140px;
  position: fixed;
  top: 0;
  background-color: #ffffff;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-left: 1px solid #B8B8B8;
  border-right: 1px solid #B8B8B8;
  border-bottom: 1px solid #B8B8B8;
}
header .container .logo {
  margin-left: 170px;
  display: flex;
}
header .container .nav_phone-wrapper {
  display: flex;
  height: 100%;
  align-items: center;
}
header .container .nav_phone-wrapper nav ul {
  display: flex;
  margin: 0 20px;
}
header .container .nav_phone-wrapper nav ul li {
  list-style-type: none;
}
header .container .nav_phone-wrapper nav ul li a {
  font-size: 25px;
  font-weight: 700;
  color: #124948;
  padding: 10px 25px;
  margin: 5px;
  display: inline-block;
  letter-spacing: 0.05em;
}
header .container .nav_phone-wrapper .phone {
  display: flex;
  align-items: center;
  padding: 0 40px;
  background-color: rgba(255, 210, 158, 0.09);
  height: 100%;
  border-left: 1px solid #B8B8B8;
}
header .container .nav_phone-wrapper .phone a {
  font-weight: 700;
  font-size: 25px;
  color: #124948;
  letter-spacing: 0.05em;
}
header .container .hamburger {
  display: none;
}

.hero {
  width: 100%;
  height: 740px;
  display: flex;
}
.hero .text {
  width: 680px;
  flex-shrink: 0;
  background-color: #B7D333;
  display: flex;
  align-items: center;
}
.hero .text .text-wrapper {
  margin-left: 180px;
  margin-right: 100px;
  display: flex;
  flex-direction: column;
}
.hero .text .text-wrapper h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: 55px;
  text-transform: uppercase;
  line-height: 40px;
  display: inline-block;
}
.hero .text .text-wrapper h1 span {
  color: #ffffff;
  line-height: 40px;
  border-bottom: 1px solid #ffffff;
  padding-bottom: 10px;
  letter-spacing: 0.05em;
}
.hero .text .text-wrapper h2 {
  color: #ffffff;
  margin-top: 32px;
  font-weight: 600;
  font-size: 30px;
}
.hero .text .text-wrapper button {
  color: #ffffff;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #CCE06E;
  border: 1px solid #ffffff;
  margin-top: 20px;
  cursor: pointer;
}
.hero .text .text-wrapper button:hover {
  background-color: rgba(255, 172, 75, 0.32);
}
.hero .picture {
  width: 100%;
  flex-shrink: 1;
}
.hero .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.products {
  width: 100%;
}
.products .product {
  display: flex;
  justify-content: space-between;
}
.products .product.no-frame {
  padding: 60px 180px;
  background-color: rgba(255, 210, 158, 0.09);
  height: 570px;
}
.products .product.frame {
  margin: 95px 180px;
  height: 500px;
}
.products .product.frame .text {
  border-top: 1px solid #8C8C8C;
  border-bottom: 1px solid #8C8C8C;
  border-left: 1px solid #8C8C8C;
  padding-left: 100px;
}
.products .product .text {
  height: 100%;
  width: 850px;
  overflow: hidden;
  padding-right: 150px;
  line-clamp: 4;
  box-orient: vertical;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.products .product .text h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
  margin-top: 10px;
}
.products .product .text h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 60px;
  width: fit-content;
}
.products .product .text p {
  font-weight: 400;
  font-size: 20px;
  color: #000000;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 11;
  line-height: 30px;
  margin-top: 10px;
}
.products .product .picture {
  height: 100%;
  width: 720px;
}
.products .product .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.products .product.left-pic .text {
  order: 1;
  padding-left: 130px;
  border-top: 1px solid #8C8C8C;
  border-bottom: 1px solid #8C8C8C;
  border-left: none;
  border-right: 1px solid #8C8C8C;
  padding-right: 80px;
}
.products .product.left-pic .picture {
  order: 0;
  width: 710px;
}
.products .equipments {
  height: 540px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
}
.products .equipments h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.products .equipments h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 80px;
  padding-bottom: 10px;
}
.products .equipments .equipments_elem {
  display: flex;
  gap: 60px;
}
.products .equipments .equipments_elem .equipment {
  display: flex;
  flex-direction: column;
  display: inline-block;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.products .equipments .equipments_elem .equipment h3 {
  font-size: 40px;
  font-weight: 700;
  color: #B7D333;
  letter-spacing: 0.05em;
  line-height: 48px;
  width: fit-content;
  margin-top: 30px;
}

.about {
  padding: 70px 600px;
  background-color: rgba(255, 210, 158, 0.09);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.about h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.about p {
  font-weight: 400;
  font-size: 20px;
  color: #000000;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 11;
  line-height: 30px;
  margin-top: 10px;
  text-align: center;
}

.location {
  padding: 70px 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.location h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.location h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 60px;
  padding-bottom: 10px;
}

.contact {
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
}
.contact h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.contact h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 30px;
  padding-bottom: 10px;
}
.contact .form {
  border-right: 1px solid #8C8C8C;
  padding-right: 20px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.contact .form form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.contact .form form input,
.contact .form form textarea {
  border: 1px solid #8C8C8C;
  padding-right: 18px;
}
.contact .form form input {
  height: 55px;
  width: 450px;
  text-align: right;
  font-weight: 600;
  font-size: 20px;
}
.contact .form form input::placeholder {
  font-weight: 600;
  font-size: 20px;
}
.contact .form form textarea {
  height: 260px;
  width: 450px;
  text-align: right;
  font-weight: 600;
  font-size: 20px;
  padding-top: 8px;
}
.contact .form form textarea::placeholder {
  font-weight: 600;
  font-size: 20px;
}
.contact .form form p.error {
  height: 20px;
  margin-bottom: 2px;
  font-size: 12px;
  color: #ff0000;
  text-align: right;
  padding-right: 9px;
  visibility: hidden;
}
.contact .form form button {
  color: #ffffff;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #CCE06E;
  border: 1px solid #ffffff;
  margin-top: 8px;
  cursor: pointer;
}
.contact .form form button:hover {
  background-color: #CEC63B;
}
.contact .address {
  padding-left: 20px;
  width: 50%;
}
.contact .address p {
  color: #000000;
  font-weight: 600;
  font-size: 25px;
}
.contact .address p.explanation {
  font-weight: 400;
  font-size: 20px;
  max-width: 450px;
  margin-top: 10px;
}
.contact .address p.tel {
  color: #B7D333;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 40px;
  max-width: 450px;
}

footer {
  width: 100%;
  height: 100px;
  background-color: #ffffff;
}
footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 200px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-left: 1px solid #B8B8B8;
  border-right: 1px solid #B8B8B8;
  border-top: 1px solid #B8B8B8;
  background-color: rgba(255, 210, 158, 0.09);
  font-weight: 600;
  font-size: 20px;
}